<template lang="en">
<section>
    <div class="modal my-popups right fade show" role="dialog" style="display: block;">
        <loader :is-visible="isLoading"></loader>
        <div class="modal-dialog mw-1100" role="document">
            <div class="modal-content">
                <div class="modal-header mheader-user">
                    <button type="button" class="close" @click="closepopup"><i class="fa fa-times" aria-hidden="true"></i></button>
                    <h4 class="modal-title" id="myModalLabel">{{ $t('ManageCampaign') }}</h4>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                        <template v-slot:tgslot-SendType="{}">
                            <ul id="ManageCampaignTypes" class="shortcuts-menu pb-3 man_camp man_view">
                                <li :class="SendType == 'SendNow' ? 'position-relative active' : 'position-relative'">
                                    <a href="javascript:" data-toggle="tooltip" title="" data-placement="bottom" @click="onClickSendOrSchedule('SendNow')" 
                                        class="short-menu-item" data-toggle-tooltip="tooltip">
                                        <span class="left-icon-m">
                                            <i class="fa fa-send-o"></i>
                                        </span>
                                        <span class="short-item-name">
                                            <span class="w-100 float-left">Send Now</span>
                                            <span class="font-12 float-left">Send the email campaign immediately</span>
                                        </span>
                                    </a>
                                </li>
                                <li :class="SendType == 'Schedule' ? 'position-relative active' : 'position-relative'">
                                    <a href="javascript:" data-toggle="tooltip" title="" data-placement="bottom" @click="onClickSendOrSchedule('Schedule')" 
                                        class="short-menu-item" data-toggle-tooltip="tooltip">
                                        <span class="left-icon-m">
                                            <i class="fa fa-calendar-o"></i>
                                        </span>
                                        <span class="short-item-name">
                                            <span class="w-100 float-left">Schedule</span>
                                            <span class="font-12 float-left">Pick a date and time</span>
                                        </span>
                                    </a>
                                </li>
                                <li :class="SendType == 'BatchSchedule' ? 'position-relative active' : 'position-relative'">
                                    <a href="javascript:" data-toggle="tooltip" title="" data-placement="bottom" @click="onClickSendOrSchedule('BatchSchedule')"
                                        class="short-menu-item" data-toggle-tooltip="tooltip">
                                        <span class="left-icon-m">
                                            <i class="fa fa-users"></i>
                                        </span>
                                        <span class="short-item-name">
                                            <span class="w-100 float-left">Batch Schedule</span>
                                            <span class="font-12 float-left">Schedule to large groups of contacts in drip mode</span>
                                        </span>
                                    </a>
                                </li>
                            </ul>
                        </template>
                        <template v-slot:tgslot-ScheduleAtTime="{data}">
                            <div class="input-group">
                                <v-date-picker :min-date="getMinDate()" v-model="ScheduleAtTime" :minute-increment="5" class="timedate timedate_hs input-group" mode="time">
                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                        <input placeholder="HH:MM" v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                        <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-clock-o"></em></span></div>
                                    </template>
                                </v-date-picker>
                            </div>
                        </template>
                        <template v-slot:tgslot-BatchStartTime="{data}">
                            <div class="input-group">
                                <v-date-picker :min-date="getMinDate()" v-model="BatchStartTime" :minute-increment="5" class="timedate timedate_hs input-group" mode="time">
                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                        <input placeholder="Start Time" v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                        <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-clock-o"></em></span></div>
                                    </template>
                                </v-date-picker>
                            </div>
                        </template>
                        <template v-slot:tgslot-BatchEndTime="{data}">
                            <div class="input-group">
                                <v-date-picker :min-date="getMinDate()" v-model="BatchEndTime" :minute-increment="5" class="timedate timedate_hs input-group" mode="time">
                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                        <input placeholder="End Time" v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                        <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-clock-o"></em></span></div>
                                    </template>
                                </v-date-picker>
                            </div>
                        </template>
                    </dynamic-form>
                </div>
            </div>
        </div>
    </div>
    <modalSMSIntegration v-if="ShowmodalSMSIntegration" :SendThrough="SendThrough" :ProfileId="ProfileId" @close="ShowmodalSMSIntegration=false"></modalSMSIntegration>
</section>
</template>
    
<script>
import DataService from '../../../services/DataService';
import modalSMSIntegration from '../../Common/Modal/modalSMSIntegration.vue';

export default {
    components:{
        modalSMSIntegration
    },
    props: { 
        campaignId:  {
            type: Number,
            required: true
        },
        campaignName:{
            type: String,
            required: true
        },
        mailChimpTemplateId:  {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            FormSchema: [{
                    layoutType: "single",
                    Data: [{
                        astype: "SlotField",
                        name: "SendType",
                        value: "",
                        placeholder: "",
                    }]
                },
                {
                    layoutType: "double",
                    Data: [{
                        astype: "SlotField",
                        name: "Schedule",
                        value: "",
                        placeholder: "",
                        visibility: false,
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "SlotField",
                        name: "Batch",
                        value: "",
                        placeholder: "",
                        visibility: false,
                    }]
                },
                {
                    layoutType: "double",
                    group_name: this.$t('ScheduleAt'),
                    group_display_name: this.$t('ScheduleAt'),
                    Data: [{
                            astype: "DateField",
                            label: "Schedule Date",
                            name: "ScheduleAtDate",
                            value: "",
                            placeholder: "Enter Start Date",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true
                            },
                            visibility: false,
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('ScheduleTime'),
                            name: "ScheduleAtTime",
                            value: "",
                            placeholder: "Time",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true
                            },
                            visibility: false,
                        }
                    ]
                },
                {
                    layoutType: "triple",
                    group_name: this.$t('NoofRecipients'),
                    group_display_name: this.$t('NoofRecipients'),
                    Data: [{
                            astype: "DateField",
                            label: this.$t("EventStartDate"),
                            name: "BatchStartDate",
                            value: "",
                            placeholder: "Enter Start Date",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true
                            },
                            visibility: false,
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('Processstartandendtime'),
                            name: "BatchStartTime",
                            value: "",
                            placeholder: "Time",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": false
                            },
                            visibility: false,
                        },
                        {
                            astype: "SlotField",
                            label: '',
                            name: "BatchEndTime",
                            value: "",
                            placeholder: "Time",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": false
                            },
                            visibility: false,
                        }
                    ]
                },
                {
                    layoutType: "triple",
                    Data: [{
                            astype: "NumericField",
                            label: this.$t('BatchQuantity'),
                            name: "BatchQuantity",
                            value: "",
                            placeholder: "Batch Quantity",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": false
                            },
                            visibility: false,
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('RepeatAfter'),
                            name: "RepeatAfter",
                            value: "",
                            placeholder: "Repeat After",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": false
                            },
                            visibility: false,
                        },
                        {
                            astype: "SelectField",
                            label: '',
                            name: "BatchType",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [{value: "Days",name: "Days"}, {value: "Hours",name: "Hours"}, {value: "Minutes",name: "Minutes"}, {value: "Seconds",name: "Seconds" }],
                            },
                            validationRules: {
                                "required": false
                            },
                            visibility: false,
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "CheckBoxField",
                        label: this.$t('SelectDay'),
                        name: "BatchDays",
                        value: ['1', '2', '3', '4', '5', '6', '7'],
                        config: {
                            options: [{value: "1", name: "Mon"}, {value: "2", name: "Tue" }, { value: "3", name: "Wed" }, { value: "4", name: "Thu" }, { value: "5", name: "Fri"  }, {  value: "6",name: "Sat"}, {value: "7", name: "Sun"}]
                        },
                        validationRules: {
                            "required": false
                        },
                        visibility: false,
                    }]
                },
                {
                    group_name: 'Sender Details',
                    group_display_name: this.$t('Sender Details'),
                    layoutType: "double",
                    Data: [{
                            astype: "SelectField",
                            label: this.$t('SendThrough'),
                            name: "SendThrough",
                            value: "",
                            placeholder: "Select",
                            config: {
                                options: [{name:'Twilio',value:'Twilio'},{name:'Mail Chimp',value:'MailChimp'},{name:'Send Grid',value:'SendGrid'}],
                                onChange: this.onChangeSendThrough
                            },
                            validationRules: {
                                "required": true
                            }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('SendProfile'),
                            name: "SendProfile",
                            value: "",
                            placeholder: "Select",
                            config: {
                                options: [],
                                onChange: this.onChangeProfile,
                                showAddIcon: true,
                                onAddButtonClick: this.AddNewAccountCredentials,
                            },
                            validationRules: {
                                "required": true
                            }
                        }
                    ]
                },
                {
                    group_name: 'Enter SMS Details',
                    group_display_name: this.$t('EnterSMSDetails'),
                    layoutType: "double",
                    Data: [{
                            astype: "TextField",
                            label: this.$t('SenderContactNumber'),
                            name: "SenderContact",
                            value: "",
                            placeholder: "",
                            config: {},
                            validationRules: {
                                "required": true
                            }
                        },
                        {
                            astype: "TextField",
                            label: this.$t('SenderName'),
                            name: "SenderName",
                            value: "",
                            placeholder: "",
                            config: {},
                            validationRules: {
                                "required": false
                            }
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "RadioField",
                        label: this.$t(''),
                        name: "Recipient",
                        value: "1",
                        placeholder: "",
                        config: {
                            options: [{ value: "1", name: "Choose Contacts" }, { value: "2", name: "Choose Group" }],
                            onChange: this.onclickContactorGroup
                        },
                        visibility: true,
                        validationRules: {
                            "required": false
                        },
                    }]
                },
                {
                    layoutType: "double",
                    Data: [{
                            astype: "MultiSelectField",
                            label: this.$t('Contact'),
                            name: "Contact",
                            value: "",
                            mode: "tag",
                            config: {
                                options: []
                            },
                            visibility: true,
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "MultiSelectField",
                            label: this.$t('ContactGroup'),
                            name: "ContactGroup",
                            value: "",
                            mode: "tag",
                            config: {
                                options: []
                            },
                            visibility: false,
                            validationRules: {
                                "required": true
                            },
                        }
                    ]
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-primary",
                    text: "<i class='fa fa-send pr-2'></i>Send",
                    isVisible: true,
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "submit",
                    class: "btn btn-primary",
                    text: "<i class='fa fa-send pr-2'></i>Schedule",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closepopup,
                    isVisible: false,
                }
            ],
            SendThrough:'',
            ProfileId:0,
            SendType: 'SendNow',
            ScheduleAtTime: null,
            BatchStartTime: null,
            BatchEndTime: null,
            Contactlist:[],
            ShowmodalSMSIntegration: false,
        }
    },
    created: function () {
        var vm = this;
        vm.FormSchema[3].group_display_name = '';
        vm.FormSchema[4].group_display_name = '';
        vm.FormSchema[3].group_name = '';
        vm.FormSchema[4].group_name = '';
        vm.GetContacts();
        vm.buttons[1].isVisible = '';
        vm.buttons[2].isVisible = '';
        vm.buttons[3].isVisible = true;
    },
    methods: {
        closepopup() {
            var vm = this;
            vm.$parent.closeManageSMSSchedule();
        },
        onClickSendOrSchedule(type) {
            var vm = this;
            vm.ResetProperties();
            vm.SendType = type;
            if (type == 'SendNow') {
                vm.FormSchema[3].group_display_name = '';
                vm.FormSchema[4].group_display_name = '';
                vm.FormSchema[4].group_name = '';
                vm.FormSchema[3].group_name = '';
                vm.FormSchema[3].Data[0].visibility = false;
                vm.FormSchema[3].Data[1].visibility = false;
                vm.FormSchema[4].Data[0].visibility = false;
                vm.FormSchema[4].Data[1].visibility = false;
                vm.FormSchema[4].Data[2].visibility = false;
                vm.FormSchema[5].Data[0].visibility = false;
                vm.FormSchema[5].Data[1].visibility = false;
                vm.FormSchema[5].Data[2].visibility = false;
                vm.FormSchema[6].Data[0].visibility = false;
                vm.buttons[0].isVisible = true;
                vm.buttons[1].isVisible = false;
                vm.buttons[2].isVisible = false;
                vm.buttons[3].isVisible = true;
            } else if (type == 'Schedule') {
                vm.FormSchema[4].group_display_name = '';
                vm.FormSchema[4].group_name = '';
                vm.FormSchema[3].group_display_name = vm.$t('ScheduleAt');
                vm.FormSchema[3].group_name = vm.$t('ScheduleAt');
                vm.FormSchema[3].Data[0].visibility = true;
                vm.FormSchema[3].Data[1].visibility = true;
                vm.FormSchema[4].Data[0].visibility = false;
                vm.FormSchema[4].Data[1].visibility = false;
                vm.FormSchema[4].Data[2].visibility = false;
                vm.FormSchema[5].Data[0].visibility = false;
                vm.FormSchema[5].Data[1].visibility = false;
                vm.FormSchema[5].Data[2].visibility = false;
                vm.FormSchema[6].Data[0].visibility = false;
                vm.buttons[0].isVisible = false;
                vm.buttons[1].isVisible = true;
                vm.buttons[2].isVisible = true;
                vm.buttons[3].isVisible = true;
            } else {
                vm.FormSchema[4].group_display_name = vm.$t('NoofRecipients');
                vm.FormSchema[4].group_name = vm.$t('NoofRecipients');
                vm.FormSchema[3].group_display_name = '';
                vm.FormSchema[3].group_name = '';
                vm.FormSchema[3].Data[0].visibility = false;
                vm.FormSchema[3].Data[1].visibility = false;
                vm.FormSchema[4].Data[0].visibility = true;
                vm.FormSchema[4].Data[1].visibility = true;
                vm.FormSchema[4].Data[2].visibility = true;
                vm.FormSchema[5].Data[0].visibility = true;
                vm.FormSchema[5].Data[1].visibility = true;
                vm.FormSchema[5].Data[2].visibility = true;
                vm.FormSchema[6].Data[0].visibility = true;
                vm.buttons[0].isVisible = false;
                vm.buttons[1].isVisible = false;
                vm.buttons[2].isVisible = true;
                vm.buttons[3].isVisible = true;
            }
        },
        ResetProperties() {
            var vm = this;
            vm.BatchStartTime = null;
            vm.BatchEndTime = null;
            vm.ScheduleAtTime = null;
            vm.FormSchema[3].Data[0].value = '';
            vm.FormSchema[4].Data[0].value = '';
            vm.FormSchema[5].Data[0].value = '';
            vm.FormSchema[5].Data[1].value = '';
            vm.FormSchema[5].Data[2].value = '';
            vm.FormSchema[6].Data[0].value = ['1', '2', '3', '4', '5', '6', '7'];
        },
        onChangeProfile(event){
            var vm=this;
            vm.ProfileId=event.target.value;
        },
        onChangeSendThrough(event){
            var vm =this;
            vm.ProfileId=0;
            vm.SendThrough=event.target.value;
            vm.FormSchema[7].Data[1].value='';
            vm.FillProfileDropdown(vm.SendThrough);
        },
        FillProfileDropdown(type){
            var vm =this;
            vm.FormSchema[7].Data[1].config.options=[];
            var url = `id=&moduleName=CRM&type=${type}`;
            DataService.getDDLData(url+ "&search=").then(response => {
                if(response.data.DATA != undefined){
                    var options = response.data.DATA;
                    options.forEach(item=>{
                    vm.FormSchema[7].Data[1].config.options.push({name:item.name, value:item.value})
                })
                }
            });
        },
        GetContacts() {
            var vm = this;
            var url = "id=&moduleName=CRM&type=CRM_CONTACT_NUMBERS";
            DataService.getDDLData(url).then(function (response) {
                vm.FormSchema[10].Data[0].config.options = [];
                if(response.data != ''){
                   var campaigncontact = response.data.DATA;
                   campaigncontact.forEach((item, index) => {
                    vm.FormSchema[10].Data[0].config.options.push({
                        name: item.name,
                        value: item.value
                    })
                   })
                }
            })
        },
        GetContactGroup: function (){
            var vm = this;
            var url = 'id=&subModuleCode=CRM_CAMPAIGNS';
            DataService.ContactGroupListing(url).then(function (response){
                vm.Contactlist=[];
                vm.FormSchema[10].Data[1].config.options = [];
                vm.Contactlist = response.data.data;
                vm.Contactlist.forEach((item, index) =>{
                    vm.FormSchema[10].Data[1].config.options.push({
                        name: item.NAME,
                        value: item.GROUP_ID
                    })
                })
            }) 
        },
        AddNewAccountCredentials(){
            var vm =this;
            if(vm.FormSchema[7].Data[0].value==''){
                vm.ShowAlert('Please select atleast one value in send through dropdown.', "warning", true, vm.$t("Alert"));
                return false;
            }
            vm.ShowmodalSMSIntegration=true;
        },
        onclickContactorGroup: function(event){
            var vm = this;
            var value = event.target.getAttribute('data-value');
            vm.FormSchema[10].Data[1].value='';
            vm.FormSchema[10].Data[0].value='';
            if(value=='1'){
                vm.GetContacts();
                vm.FormSchema[10].Data[1].visibility=false;
                vm.FormSchema[10].Data[0].visibility=true;
            }
            else{
                vm.GetContactGroup();
                vm.FormSchema[10].Data[0].visibility=false;
                vm.FormSchema[10].Data[1].visibility=true;
            }
        },
        onSubmit: function (formFeilds, values) {
            var vm = this;
            var btnType='';
            if(values !=''){
                 var btnValueArr= values.split('</i>');
                 btnType=btnValueArr[1];
            }
            var schedule ={
                date    : formFeilds.ScheduleAtDate,
                time    : vm.ScheduleAtTime,
                timezone: null
            }
            var batch_schedule ={
                start_date      : formFeilds.BatchStartDate,
                batch_quantity  : formFeilds.BatchQuantity,
                batch_type      : formFeilds.BatchType,
                repeat_after    : formFeilds.RepeatAfter,
                batch_days      : formFeilds.BatchDays.join(),
                start_time      : vm.BatchStartTime,
                end_time        : vm.BatchEndTime
            }
            var jsonObj ={
                campaign_id     : vm.campaignId,
                sub_module_code : 'CRM_CAMPAIGNS',
                send_type       : vm.SendType,
                send_through    : formFeilds.SendThrough,
                profile_id      : formFeilds.SendProfile,
                recipient       : formFeilds.Recipient,
                from_email      : formFeilds.SenderContact,
                from_name       : formFeilds.SenderName,
                contact         : formFeilds.Contact,
                contact_group   : formFeilds.ContactGroup,
                schedule        : schedule,
                batch_schedule  : batch_schedule
            }
            var url = JSON.stringify(jsonObj);
            DataService.SaveScheduleCampaign(url).then(response => {
                if(btnType=='Save'){
                    vm.ShowAlert("SMS Save Successfully.","success", true, vm.$t("Alert"));
                    vm.closepopup();
                }
                    else{
                        if(response.data=='SENT'){
                        vm.ShowAlert("SMS Sent Successfully.","success", true, vm.$t("Alert"));
                        vm.closepopup();
                    }else if(response.data=='SCHEDULED'){
                        vm.ShowAlert("SMS Scheduled Successfully.","success", true, vm.$t("Alert"));
                        vm.closepopup();
                    }else if(response.data=='BATCH_SCHEDULED'){
                        vm.ShowAlert("SMS Batch Scheduled Successfully.","success", true, vm.$t("Alert"));
                        vm.closepopup();
                    }else{
                        vm.ShowAlert("Something went wrong.","danger", true, vm.$t("Alert"));
                    }
                }
            });
            if(formFeilds.SendThrough=='Twilio'){
                vm.GetContactListData(jsonObj);
            }
        },
        async GetContactListData(item){
            var vm = this;
            let type="";
            let ids = "";
            if(item.recipient=="1"){
                type="contacts";
                var contacts = JSON.stringify(item.contact);
                var contactArr =JSON.parse(contacts);
                contactArr.forEach((x,i) => {
                    if(i==0){
                        ids =  x.value;
                    }
                    else{
                        ids +=  "," + x.value;
                    }
                })
            }
            else{
                type= "groups";
                var group = JSON.stringify(item.contact_group);
                var contactGrpArr =JSON.parse(group);
                contactGrpArr.forEach((x,i)=>{
                    if(i==0){
                        ids =  x.value;
                    }
                    else{
                        ids +=  "," + x.value;
                    }
                })
            }
            var jsonObj={
                type: type,
                ids:ids
            }
            var url = JSON.stringify(jsonObj);
            await DataService.GetContacts(url).then(async response => {
                var result = response.data.RESULT;
                if(response.data.STATUS=='SUCCESS' && result !=null){
                await vm.AddMemberToMailChimp(result);
                await vm.SaveSendMaliChimpCampaign(item);
                }
            });
        },
        async AddMemberToMailChimp(jsonObj){
            await DataService.AddMemberToMailChimp(jsonObj).then(response => {
            });
        },
        async SaveSendMaliChimpCampaign(item){
            var vm=this;
            var repeatAfter='';
            let batchType = item.batch_schedule.batch_type;
            const time = parseInt(item.batch_schedule.repeat_after);
            if(batchType=='Seconds'){
                repeatAfter = Math.floor(time / 60);  //Convert seconds to minutes
            }
            else if(batchType=='Hours'){
                repeatAfter = Math.floor(time * 60);  //Convert hours to minutes
            }
            else if(batchType=='Days'){
                repeatAfter = Math.floor(time * 24 * 60);  //Convert days to minutes
            }else{
                repeatAfter =time; //The delay, in minutes, between batches.
            }
            var jsonObj ={
                templateId:vm.mailChimpTemplateId,
                fromName:item.from_name,
                subjectLine:item.subject,
                issueTitle:vm.campaignName,
                previewText:item.preview_text,
                fromEmail:item.from_email,
                sendType: item.send_type,
                scheduleDate:item.schedule.date,
                scheduleTime:item.schedule.time,
                batchStartDate:item.batch_schedule.start_date,
                batchStartTime:item.batch_schedule.start_time,
                batchQuantity:item.batch_schedule.batch_quantity,
                repeatAfter:repeatAfter
            }
            var url = JSON.stringify(jsonObj);
            DataService.AddCampaignToMailChimp(url).then(response => {
            });
        }
    }
}
</script>
    
